.profile {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 90vw;
  grid-gap: 15px;
  margin: auto;
  grid-template-areas: "left-top right right right" "left-bottom right right right"; }
  .profile .pic-grid {
    grid-area: left-top; }
    .profile .pic-grid img {
      width: 100%;
      height: auto; }
    .profile .pic-grid h2 {
      margin: 5px 0;
      text-align: center; }
  .profile .comments-grid {
    grid-area: left-bottom; }
  .profile .posts-grid {
    grid-area: right; }
    .profile .posts-grid h2 {
      text-align: center;
      margin: 0 0 20px 0; }

@media only screen and (max-width: 767px) {
  .profile {
    grid-template-columns: 1fr;
    grid-gap: 50px 0px;
    grid-template-areas: "left-top" "right" "left-bottom";
    width: 100vw; }
  .pic-grid {
    margin: 0 120px; } }

.posts-container {
  display: flex;
  flex-direction: column;
  background-color: var(--background2);
  border-radius: 10px;
  padding: 10px 0;
  overflow-y: scroll;
  max-height: 80vh; }
  .posts-container .post-container {
    background: var(--background1);
    border-radius: 5px;
    margin: 0 15px 15px 15px;
    padding: 10px;
    vertical-align: center; }
    .posts-container .post-container .post-title {
      font-size: 40px;
      color: var(--stroke);
      font-weight: 600;
      background: var(--background);
      font-size: 22px;
      display: flex;
      justify-content: space-between; }
      .posts-container .post-container .post-title .title:hover {
        cursor: pointer;
        text-decoration: underline; }
      .posts-container .post-container .post-title .date {
        font-size: 15px;
        color: var(--paragraphy); }
    .posts-container .post-container .post-body {
      font-size: 18px;
      color: var(--paragraphy); }
    .posts-container .post-container .post-footer {
      font-weight: 600;
      color: var(--stroke);
      text-align: right;
      padding-right: 2px;
      font-size: 18px; }
      .posts-container .post-container .post-footer span {
        cursor: pointer; }
        .posts-container .post-container .post-footer span:hover {
          text-decoration: underline; }

